import axios from '@/libs/api.request'

// 
export const socketInfomation = (params) => {
	return axios.request({
		url: '/cperm/cent/refresh',
		params,
		method: 'get',
	})
}

// 
export const unRead = () => {
	return axios.request({
		url: 'cperm/notice_msg/unread_count',
		method: 'get',
	})
}

// 
export const infoList = (
	p_index,
	p_size,
	state) => {
	return axios.request({
		url: 'cperm/notice_msg',
		method: 'get',
		params: {
			p_index,
			p_size,
			state
		}
	})
}

// 
export const toRead = () => {
	return axios.request({
		url: '/cperm/notice_msg',
		method: 'patch',
		data: []
	})
}
